
export default {
  props: {
    copy: {
      type: Function,
      required: true,
    },
    buttonText: {
      type: String,
      default: 'Duplicate',
    },
    buttonIcon: {
      type: String,
      default: 'mdi-clipboard-multiple-outline',
    },
    tooltipText: {
      type: String,
      default: 'Duplicate',
    },
    confirmText: {
      type: String,
      default: null,
    }
  },
  methods: {
    async performCopy() {
      await this.copy();
      this.$toast.success('Duplicated successfuly');
    }
  }
}
